body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.recipe_recipe__2aoT- {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.recipe_image__1QmJ7 {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}
.App {
  min-height: 100vh;
  background-image: -webkit-linear-gradient(45deg, #a18cd1 0%, #fbc2eb 100%);
  background-image: linear-gradient(45deg, #a18cd1 0%, #fbc2eb 100%);
}

.search-form {
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-content: center;
}

.search-bar { 
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white; 
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
